import React from "react";
import styles from "./Bank.module.scss";
import { useParams } from "react-router-dom";
import Title from "../title/Title";
import { format_price } from "../../functions";
import { useDispatch, useSelector } from "react-redux";
import { selectAppData, setCancel, setSuccess } from "../main/MainSlice";
import Tip from "../tip/Tip";
import Card from "../card/Card";
import { banks_data } from "../../const";
import Timer from "../timer/Timer";
import Button from "../button/Button";


export default function Bank() {
  const dispatch = useDispatch();
  const {bank} = useParams();
  const {price} = useSelector(selectAppData);

  return (
  <div className={styles.cont}>
    <Title>    <b>{format_price(price)} </b></Title>
    <div className={styles.block}>
      <Tip>
        <> </>
        <> </>
        <> </>
      </Tip>
      <div className={styles.split}>
        <div className={styles.left}>
          <Card bank={banks_data[bank]} />
          <Timer />
        </div>
        <div className={styles.right}>
          <p>      <b>{format_price(price)} </b>, <b></b>  <b> </b> !</p>
          <p>   <b> </b>.</p>
          <p>      .  <b></b>  , <b>  </b>.   ,  <b>  !</b></p>
          <p>     </p>
        </div>
      </div>
    </div>
    <div className={styles.btnBlock}>
      <Button color="purple" onClick={() => {dispatch(setSuccess());}}> </Button>
      <Button color="transparent" onClick={() => {dispatch(setCancel());}}></Button>
    </div>
  </div>
  );
}